// Font weights
$light: 100;
$regular: 400;
$bold: 600;

// Base Font
$base-font-family: 'Courgette';
$base-font-weight: $regular;
$base-font-size: 1rem;
$base-line-height: 1.25;

// Fixed Font
$fixed-font-family: monospace;
$fixed-font-size: 85%;
$fixed-line-height: $base-line-height;

// Headings
$header-font-weight: $bold;
$header-font-size: 1.75rem;

@import "modules/all";
